/*
    example result :
    .blue-500 {
      --color     : #1192ee;
      --color-alt : #1083d5;
    }
  */
:root {
  --light-color: var(--grey-300);
  --border-color: var(--grey-300);
  --title-color: var(--grey-700);
  --text-color: var(--grey-600);
  --text-color-light: var(--grey-500);
  --text-color-lighter: var(--grey-400);
  --color-light: var(--light-color);
  --color-border: var(--border-color);
  --color-title: var(--title-color);
  --color-text: var(--text-color);
  --color-text-light: var(--color-text-light);
  --color-text-lighter: var(--text-color-lighter);
  --body-bg: var(--grey-100);
  --color: var(--blue-500);
  --color-alt: var(--blue-600);
  --white-color: #fff;
  --transparent: transparent;
}

.white-color,
.color-white {
  --color: var(--white-color);
}

.text-color,
.color-text {
  --color: var(--text-color);
  --color-alt: var(--dark-color);
}

.text-color-light,
.color-text-light {
  --color: var(--text-color-light);
  --color-alt: var(--text-color);
}

.text-color-lighter,
.color-text-lighter {
  --color: var(--text-color-lighter);
  --color-alt: var(--text-color-light);
}

.light-color,
.color-light {
  --color: var(--light-color);
  --color-alt: var(--border-color);
}

.border-color {
  --color: var(--border-color);
}

.body-bg {
  --color: var(--body-bg);
}

.color {
  color: var(--color);
}

.background {
  background-color: var(--color);
}

.fill {
  fill: var(--color);
}

.border {
  --border-color: var(--color);
}

.hover-bg:hover {
  background-color: var(--color-alt);
}

.hover-text:hover {
  color: var(--color-alt);
}

.my-grid {
  opacity: 0;
  transition: opacity 0.5s linear 1s;
}

.my-grid.my-grid-visible {
  opacity: 1;
}

.item-shadow {
  background: #eeeeee;
  padding-top: 10px;
}
